import axios from 'axios'
import { Message } from 'element-ui'

const host = location.host
// let apiUrl = 'http://192.168.10.105:1010/v1_1/'
console.log('host', `http://${host}/api/`)
//
// axios.get('http://0.0.0.0:5003/api').then(res => {
//   apiUrl = res.data
// })


const serviceScaner = axios.create({
  // baseURL: 'http://192.168.10.105:8000/v1_1/'
    baseURL: `http://${host}/api/`
})

serviceScaner.interceptors.request.use(
  config => config,
  error => {
    console.log(error)
    return Promise.reject(error)
  }
)
serviceScaner.interceptors.response.use(
  response => response,
  error => {
    console.log('err' + error)
    Message({
      message: error.message,
      type: 'error'
    })
    return Promise.reject(error)
  }
)

export default serviceScaner



