import axios from 'axios'
import { Message } from 'element-ui'

const serviceSnap = axios.create({
  baseURL: 'http://192.168.10.105:8000/v1.1/snapshot'
})

serviceSnap.interceptors.request.use(
  config => config,
  error => {
    console.log(error)
    return Promise.reject(error)
  }
)
serviceSnap.interceptors.response.use(
  response => response,
  error => {
    console.log('err' + error)
    Message({
      message: error.message,
      type: 'error'
    })
    return Promise.reject(error)
  }
)

export default serviceSnap



