import request from '@/utils/request'

export function fetchList(query) {
  return request.scaner({
    url: '/storage',
    method: 'get',
    params: query
  })
}

export function fetchImage(requestImage) {
  return request.scaner({
    url: '/scanner',
    method: 'post',
    data: requestImage
  })
}
export function fetchSecurity(requestImage) {
  return request.scaner({
    url: '/scanner/image',
    method: 'post',
    data: requestImage
  })
}
export function fetchImageDetail(requestImage) {
  return request.scaner({
    url: '/scanner/detail',
    method: 'post',
    data: requestImage
  })
}

export function getImageList() {
  return request.scaner({
    url: '/repository',
    method: 'GET'
  })
}
export function createImage(data) {
  return request.scaner({
    url: '/repository',
    method: 'POST',
    data
  })
}
export function updateImage(data) {
  return request.scaner({
    url: '/repository',
    method: 'PUT',
    data
  })
}
export function deleteImage(repository) {
  return request.scaner({
    url: `/repository?repository=${repository}`,
    method: 'DELETE'
  })
}
export function updateScore(data) {
  return request.scaner({
    url: '/score',
    method: 'PUT',
    data
  })
}
export function getScore() {
  return request.scaner({
    url: '/score',
    method: 'GET'
  })
}

export function fetchCVE(url, page) {
  return request.scaner({
    // url: '/pages/${url}?page=${page}',
    url: `/pages/${url}`,
    method: 'get',
    params: {
      page
    }
  })
}

export function pullImage(name) {
  return request.scaner({
    url: '/images_pull?',
    method: 'get',
    params: {
      name
    }
  })
}

export function searchImage(name) {
  return request.scaner({
    url: '/search',
    method: 'get',
    params: {
      name
    }
  })
}

export function getTagNum(name) {
  return request.scaner({
    url: '/images',
    method: 'get',
    params: {
      image_name: name
    }
  })
}

export function getProgress(name) {
  return request.scaner({
    url: '/progress',
    method: 'get',
    params: {
      name
    }
  })
}

export function stopLoadImage(name) {
  return request.scaner({
    url: '/stop',
    method: 'get',
    params: {
      name
    }
  })
}



