<template>
  <div class="nav">
    <router-link to="/">
      <img class="logo" src="./logo.png" alt="logo">
    </router-link>
    <span class="logo-text">DeepDefense容器安全监测平台</span>
    <router-link to="/">
      <span class="primary-btn">容器镜像扫描</span>
    </router-link>
    <router-link to="/monitorView">
      <span class="primary-btn">容器运行监控</span>
    </router-link>
    <router-link to="/shieldView">
      <span class="primary-btn">容器网络防护</span>
    </router-link>
    <slot></slot>
    <div class="nav-right">
      <span>管理员</span>
      <el-dropdown class="avatar-container right-menu-item" trigger="hover"
                   :show-timeout="10" :hide-timeout="200">
        <div class="avatar-wrapper">
          <img class="avatar" src="./avatar.png"/>
          <i class="el-icon-caret-bottom"></i>
        </div>
        <el-dropdown-menu slot="dropdown">
          <router-link to="/">
            <el-dropdown-item>
              系统设置
            </el-dropdown-item>
          </router-link>
          <el-dropdown-item divided>
                <span @click="isShow = true" class="exit-span">
                  <i class="icon-signOut"></i>
                  退出
                </span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .nav
    width: 100%
    height: 68px
    position: absolute
    background: #1D2B40

  .logo
    width 73px
    margin: 18px 10px 0px 50px

  .logo-text
    font-size: 1.6em
    font-weight: 600
    color: #FFF
    position: relative
    top: 18%

  .primary-btn
    font-size: 1.6em
    font-weight: 600
    color: #FFF
    position: relative
    top: 18%
    margin 0px -87px 0px 100px

  .primary-btn:hover
    color #409EFF

  .nav-right
    float: right

  .nav-right span
    color: #fff
    font-size: 1.4em
    font-weight: normal
    display: inline-block
    margin: 16px 10px 0 0

  .avatar
    margin: 13px 30px 0 15px

  .nav-right div i
    padding: 3px 25px 0 0px

  .avatar-container
    height: 50px
    margin-right: 30px
    .avatar-wrapper
      cursor: pointer
      position: relative
      .user-avatar
        width: 40px
        height: 40px
        border-radius: 10px
      .el-icon-caret-bottom
        position: absolute
        right: -20px
        top: 25px
        font-size: 12px

  .exit-span
    display block
</style>



