import axios from 'axios'
import {
  Message
} from 'element-ui'

const serviceScanerAddImage = axios.create({
  baseURL: 'http://gushenxing.f3322.net:17007/api/v1_0/'
})
serviceScanerAddImage.interceptors.request.use(
  config => config,
  error => {
    console.log(error)
    return Promise.reject(error)
  }
)
serviceScanerAddImage.interceptors.response.use(
  response => response,
  error => {
    console.log('err' + error)
    Message({
      message: error.message,
      type: 'error'
    })
    return Promise.reject(error)
  }
)

export default serviceScanerAddImage



