import axios from 'axios'
import { Message } from 'element-ui'

const serviceRules = axios.create({
  baseURL: 'http://192.168.10.105:8000/v1_1/rules/'
})

serviceRules.interceptors.request.use(
  config => config,
  error => {
    console.log(error)
    return Promise.reject(error)
  }
)
serviceRules.interceptors.response.use(
  response => response,
  error => {
    console.log('err' + error)
    Message({
      message: error.message,
      type: 'error'
    })
    return Promise.reject(error)
  }
)

export default serviceRules



